import {
    Button,
    Container,
    FormControl,
    Grid,
    ListItem,
    ListItemText,
    Paper,
    TextField,
    Typography
} from "@mui/material";
import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';
import coineusone_abi from "../abis/coineusone_abi.json";
import coineus_rewards_abi from "../abis/coineus_rewards.json";
import erc20_abi from "../abis/IERC20.json";

import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {formatWeiToNumber} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";

const COINEUS_FUND_ADDRESS = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";

export default function RewardsLocker() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [dataFUSE, setDataFUSE] = useState({});
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");

    const getInfo = () => {

        const web3FUSE = new Web3(rpc.fuse);

        const fusd_contract = new web3FUSE.eth.Contract(erc20_abi, "0x249BE57637D8B013Ad64785404b24aeBaE9B098B");
        const coineusone_contract = new web3FUSE.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");

        const callsFUSE = [
            fusd_contract.methods.balanceOf(coineusone_contract._address).call,
            fusd_contract.methods.balanceOf('0x836c205C49D9Db143f0A79DD384d08895f171C5f').call,//cnsvolt
            fusd_contract.methods.balanceOf('0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48').call, //cnsbnb
            fusd_contract.methods.balanceOf('0xeFA4222Fd80328558801Fe78ba3392048432d891').call, //cnsbnb
        ]

        makeBatchRequest(web3FUSE, callsFUSE).then(resp => {
            let [
                fusd_balance_coineusone,
                cnsvolt_fusd,
                cnsbnb_fusd,
                cnsfuse_fusd,
            ] = resp;

            cnsvolt_fusd = cnsvolt_fusd / 10 ** 18;
            cnsbnb_fusd = cnsbnb_fusd / 10 ** 18;
            cnsfuse_fusd = cnsfuse_fusd / 10 ** 18;

            setDataFUSE({
                fusd_balance_coineusone,
                cnsvolt_fusd,
                cnsbnb_fusd,
                cnsfuse_fusd,
            })

        })
    }

    const claimFUSD = () => {

        const web3FUSE = new Web3(window.provider);
        const coineusone_contract = new web3FUSE.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");

        coineusone_contract.methods.claimRandomToken("0x249BE57637D8B013Ad64785404b24aeBaE9B098B")
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });

    }

    const rCNSONEToEveryone = () => {
        const web3FUSE = new Web3(window.provider);
        const coineusone_contract = new web3FUSE.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");

        coineusone_contract.methods.rCNSONEToEveryone(web3FUSE.utils.toBN(amount * (10 ** 18)))
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    const processDividendTracker = (contract) => {
        const web3FUSE = new Web3(window.provider);
        const coineus_contract = new web3FUSE.eth.Contract(coineus_abi, contract);
        coineus_contract.methods.processDividendTracker(web3FUSE.utils.toBN((10 ** 18)))
            .send({from: wallet.address, gasLimit: 18000000, gasPrice: 10000000000})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    const ownerSwap = (contract) => {
        const web3FUSE = new Web3(window.provider);
        const coineus_contract = new web3FUSE.eth.Contract(coineus_rewards_abi, contract);
        coineus_contract.methods.ownerSwap()
            .send({from: wallet.address, gasPrice: 10000000000})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });

    }

    useEffect(getInfo, [wallet.address]);

    return <Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

            {
                wallet.address === COINEUS_FUND_ADDRESS && <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Coineus ONE fUSD</Typography>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(dataFUSE.fusd_balance_coineusone)) + " fUSD"}
                                secondary="Claimable"
                            />
                        </ListItem>
                        <Button variant="contained"
                                disabled={wallet.chain !== 122} fullWidth style={{marginTop: 20}}
                                onClick={claimFUSD}>Claim</Button>
                    </Paper>
                </Grid>
            }


            {
                wallet.address === COINEUS_FUND_ADDRESS && <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Reflect CNSONE</Typography>
                        <FormControl fullWidth style={{marginBottom: 20, marginTop: 20}}>
                            <TextField
                                autoComplete="off"
                                type="number"
                                value={amount}
                                placeholder="Enter Amount"
                                onChange={(ev) => {
                                    setAmount(ev.target.value)
                                }}
                            />
                        </FormControl>
                        <Button fullWidth variant="contained"
                                disabled={!amount}
                                onClick={rCNSONEToEveryone}>REFLECT CNSONE TO EVERYONE</Button>
                    </Paper>
                </Grid>
            }


            {
                wallet.address === COINEUS_FUND_ADDRESS && <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Coineus Dividend Tracker</Typography>

                        <Button fullWidth variant="contained"
                                onClick={() => processDividendTracker("0xc2E299b47398963c618De5b05C6BdeCd4cc64022")}>PROCESS</Button>
                    </Paper>
                </Grid>
            }


            {
                wallet.address === COINEUS_FUND_ADDRESS && <>

                    <Grid item xs={12}>
                        <Paper style={{padding: 20}}>
                            <Typography variant="h6">CNSfuse fUSD</Typography>

                            <Grid container spacing={2}>
                                <Grid item xs={6}>
                                    <Button fullWidth variant="contained" style={{backgroundColor: '#3d668d'}}
                                            onClick={() => ownerSwap('0xeFA4222Fd80328558801Fe78ba3392048432d891')}>SWAP {CoineusUSDFormat(dataFUSE.cnsfuse_fusd)}</Button>
                                </Grid>
                                <Grid item xs={6}>
                                    <Button fullWidth variant="contained" style={{backgroundColor: '#3d668d'}}
                                            onClick={() => processDividendTracker("0xeFA4222Fd80328558801Fe78ba3392048432d891")}>PROCESS</Button>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid>

                    <Grid item xs={12}>
                        <Paper style={{padding: 20}}>
                            <Typography variant="h6">CNSvolt fUSD</Typography>

                            <Grid container spacing={2}>
                                <Grid item xs={6}>
                                    <Button fullWidth variant="contained" style={{backgroundColor: '#65CA00'}}
                                            onClick={() => ownerSwap('0x836c205C49D9Db143f0A79DD384d08895f171C5f')}>SWAP {CoineusUSDFormat(dataFUSE.cnsvolt_fusd)}</Button>
                                </Grid>
                                <Grid item xs={6}>
                                    <Button fullWidth variant="contained" style={{backgroundColor: '#65CA00'}}
                                            onClick={() => processDividendTracker("0x836c205C49D9Db143f0A79DD384d08895f171C5f")}>PROCESS</Button>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid>

                    <Grid item xs={12}>
                        <Paper style={{padding: 20}}>
                            <Typography variant="h6">CNSbnb fUSD</Typography>

                            <Grid container spacing={2}>
                                <Grid item xs={6}>
                                    <Button fullWidth variant="contained" style={{backgroundColor: '#fbca00'}}
                                            onClick={() => ownerSwap('0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48')}>SWAP {CoineusUSDFormat(dataFUSE.cnsbnb_fusd)}</Button>
                                </Grid>
                                <Grid item xs={6}>
                                    <Button fullWidth variant="contained" style={{backgroundColor: '#fbca00'}}
                                            onClick={() => processDividendTracker("0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48")}>PROCESS</Button>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid>
                </>
            }
        </Grid>

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Container>
}