import * as React from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";
import {Paper, Typography, ListItem, ListItemIcon, ListItemText, Skeleton, Grid} from "@mui/material";
import {makeStyles} from "@mui/styles";
import {Autorenew, Whatshot, Shower, AccountBalance, Bolt, SwapHorizontalCircle, SwapVerticalCircle} from "@mui/icons-material";
import {CoineusCryptoFormat} from "../utils/currency_format";

const useStyles = makeStyles((theme) => ({
    xyplot: {
        margin: '0 auto',
    },
}));

function BurnSupply(props) {
    const classes = useStyles();

    const {burnt, supply, percent, cnsone, faucet, fund, liquidity, cnsvolt, cnsbnb, cnsfuse} = props;

    const divider = 360 * .0075 * (Math.PI / 180);

    const _percent = (supply - faucet - cnsone - cnsvolt - cnsbnb - cnsfuse - fund - liquidity) / supply;
    const aSupply = (360 * _percent * (Math.PI / 180)) - (divider * 4);

    const percent2 = cnsone ? (cnsone - burnt) / supply : 0;
    const aCnsone = 360 * percent2 * (Math.PI / 180);

    const burntValue = (percent * 100).toFixed(2);
    const percentStaked = cnsone ? (((cnsone + cnsvolt + cnsbnb + cnsfuse) / supply) * 100).toFixed(2) : 0;

    const percent3 = faucet / supply;
    const aFaucet = 360 * percent3 * (Math.PI / 180);

    const percent4 = fund / supply;
    const aFund = 360 * percent4 * (Math.PI / 180);

    const percent5 = liquidity / supply;
    const aLiquidity = 360 * percent5 * (Math.PI / 180);

    const percent6 = cnsvolt / supply;
    const aCnsvolt = 360 * percent6 * (Math.PI / 180);

    const percent8 = cnsbnb / supply;
    const aCnsbnb = 360 * percent8 * (Math.PI / 180);

    const percent10 = burnt / supply;
    const aBurnt = 360 * percent10 * (Math.PI / 180);

    const percent11 = cnsfuse / supply;
    const aCnsfuse = 360 * percent11 * (Math.PI / 180);

    const colors = {
        label: '#fff',
        supply: '#fbca00',
        burned: '#fa5700',
        faucet: '#1c6fe0',
        fund: '#ff00eb',
        liquidity: '#a100ff',

        staked: '#ffe35c',
        cnsone: '#ff0000',
        cnsvolt: '#65CA00',
        cnsbnb: '#fbca00',
        cnsfuse: '#3d668d'
    }

    if (
        burnt === undefined ||
        supply === undefined ||
        percent === undefined ||
        cnsone === undefined ||
        faucet === undefined ||
        fund === undefined ||
        liquidity === undefined
    ) {
        return <Paper style={{padding: 20}}>
            <Skeleton variant="circular" height={180} width={180} style={{margin: '0 auto'}}/>
        </Paper>;
    }

    return (
        <Paper style={{padding: 20}}>
            <FlexibleWidthXYPlot
                xDomain={[-5, 5]}
                yDomain={[-5, 5]}
                height={250}
                margin={{left: 0, right: 0, top: 0, bottom: 0}}
                className={classes.xyplot}
            >
                <ArcSeries
                    radiusDomain={[0, 10]}
                    center={{x: 0, y: 0}}
                    data={[
                        {
                            angle0: aBurnt + divider,
                            angle: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + (divider * 4),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.staked,
                        },
                        {
                            angle0: 0,
                            angle: aBurnt,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.burned,
                        },
                        {
                            angle0: 0,
                            angle: aCnsone + (divider),
                            opacity: 1,
                            radius: 7.5,
                            radius0: 7.25,
                            color: colors.cnsone,
                        },
                        {
                            angle0: aCnsone + (divider * 2),
                            angle: aCnsone + aCnsvolt + (divider * 2),
                            opacity: 1,
                            radius: 7.5,
                            radius0: 7.25,
                            color: colors.cnsvolt,
                        },
                        {
                            angle0: aCnsone + aCnsvolt + (divider * 3),
                            angle: aCnsone + aCnsvolt + aCnsbnb + (divider * 3),
                            opacity: 1,
                            radius: 7.5,
                            radius0: 7.25,
                            color: colors.cnsbnb,
                        },
                        {
                            angle0: aCnsone + aCnsvolt + aCnsbnb + (divider * 4),
                            angle: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse +(divider * 4),
                            opacity: 1,
                            radius: 7.5,
                            radius0: 7.25,
                            color: colors.cnsfuse,
                        },
                        {
                            angle0: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + (divider * 5),
                            angle: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + aSupply + (divider * 5),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.supply,
                        },
                        {
                            angle0: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + aSupply + (divider * 6),
                            angle: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + aSupply + aFaucet + (divider * 6),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.faucet,
                        },
                        {
                            angle0: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + aSupply + aFaucet + (divider * 7),
                            angle: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + aSupply + aFaucet + aFund + (divider * 7),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.fund,
                        },
                        {
                            angle0: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + aSupply + aFaucet + aFund + (divider * 8),
                            angle: aCnsone + aCnsvolt + aCnsbnb + aCnsfuse + aSupply + aFaucet + aFund + aLiquidity + (divider * 11),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.liquidity,
                        }
                    ]}
                    colorType={'literal'}
                />
            </FlexibleWidthXYPlot>

            <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -125,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700'
                }}
            >
                {burntValue}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % BURNED
        </span>
            </Typography>
            {cnsone !== 0 && <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -180,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700',
                }}
            >
                {percentStaked}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % DEPOSITED
        </span>
            </Typography>}

            <Grid container>
                <Grid item xs={6}>
                    <StatListItem icon={<Bolt style={{color: colors.label}}/>} color={colors.label} value={supply}
                                                label="TOTAL SUPPLY"/>
                </Grid>
                <Grid item xs={6} style={{backgroundColor: colors.staked, borderRadius: '10px 10px 0 0'}}>
                    <StatListItem icon={<SwapVerticalCircle style={{color: "#000"}}/>} color={"#000"} value={cnsone + cnsvolt + cnsbnb + cnsfuse}
                                  label="DEPOSITED"/>
                </Grid>
            </Grid>

            <Grid container style={{border: `1px solid ${colors.staked}`, borderRadius: '10px 0 10px 10px', padding: '10px 0', marginBottom: 10 }}>
                <Grid item xs={6}>
                    <StatListItem color={colors.cnsone} value={cnsone} label="COINEUS ONE"/>
                    <StatListItem color={colors.cnsbnb} value={cnsbnb} label="CNSbnb"/>
                </Grid>
                <Grid item xs={6}>
                    <StatListItem color={colors.cnsvolt} value={cnsvolt} label="CNSvolt"/>
                    <StatListItem color={colors.cnsfuse} value={cnsfuse} label="CNSfuse"/>
                </Grid>
            </Grid>

            <Grid container>
                <Grid item xs={6}>
                    <StatListItem icon={<Autorenew style={{color: colors.supply}}/>} color={colors.supply}
                                  value={supply - faucet - burnt - cnsone - cnsvolt - cnsbnb - cnsfuse - fund - liquidity} label="CIRCULATING"/>
                    <StatListItem icon={<Shower style={{color: colors.faucet}}/>} color={colors.faucet} value={faucet}
                                  label="FAUCET"/>
                    <StatListItem icon={<SwapHorizontalCircle style={{color: colors.liquidity}}/>} color={colors.liquidity}
                                  value={liquidity} label="LIQUIDITY"/>
                </Grid>
                <Grid item xs={6}>
                    <StatListItem icon={<Whatshot style={{color: colors.burned}}/>} color={colors.burned} value={burnt}
                                  label="BURNED"/>
                    <StatListItem icon={<AccountBalance style={{color: colors.fund}}/>} color={colors.fund} value={fund}
                                  label="FUND STORAGE"/>
                </Grid>
            </Grid>

        </Paper>
    );
}

export default BurnSupply;

function StatListItem(props) {
    const {color, value, label, icon} = props;

    return <ListItem disablePadding>
        <ListItemIcon style={{paddingLeft: 15}}>
            {icon}
        </ListItemIcon>
        <ListItemText
            primary={CoineusCryptoFormat(value)}
            secondary={label}
            primaryTypographyProps={{fontWeight: 700, style: {color}}}
            secondaryTypographyProps={{fontSize: '.75rem', style: {color}}}
        />
    </ListItem>;
}
