import {Alert, Button, Container, Divider, Grid, ListItem, ListItemText, Paper} from "@mui/material";

import faucet_abi from '../abis/fuse_faucet.json';
import {useSelector} from "react-redux";
import Web3 from "web3";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {formatWeiToNumber} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";

const FAUCET_ADDRESS = '0xFbE136842930Ed73a2E315b65f916Dfd1DBa4595';

export default function FUSEFaucet() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [pending, setPending] = useState(false);
    const [faucetData, setFaucetData] = useState(undefined);
    const [userFaucetData, setUserFaucetData] = useState(undefined);
    const [disabled, setDisabled] = useState(false);
    const [disabledReason, setDisabledReason] = useState(undefined);

    const web3FUSE = new Web3(rpc.fuse);
    const faucet_contract = new web3FUSE.eth.Contract(faucet_abi, FAUCET_ADDRESS);

    const getInfo = () => {
        const calls = [
            faucet_contract.methods.getClaimPaused().call,
            faucet_contract.methods.getClaimableAmount().call,
            faucet_contract.methods.getMinAmountToClaim().call,
            faucet_contract.methods.getStats().call,
            faucet_contract.methods.getMinAmountToClaim().call,
            faucet_contract.methods.getDelegatedAmount().call
        ]

        makeBatchRequest(web3FUSE, calls).then(resp => {
            let [
                paused,
                claimable,
                minBalance,
                stats,
                minToClaim,
                delegatedAmount
            ] = resp;

            setFaucetData({
                paused,
                claimable,
                minBalance,
                stats,
                minToClaim,
                delegatedAmount
            })

        })
    }

    const getUserInfo = () => {

        if (!wallet.address) {
            return;
        }

        const calls = [
            faucet_contract.methods.getStatsByAddress(wallet.address).call
        ]

        makeBatchRequest(web3FUSE, calls).then(resp => {
            let [
                stats
            ] = resp;

            setUserFaucetData({
                stats
            })

        })
    }

    useEffect(()=> {

        if(!wallet.address) {
            setDisabled(true)
            setDisabledReason(`Connect wallet to claim`)
            return;
        }

        if(
            faucetData &&
            faucetData.paused) {

            setDisabled(true);
            setDisabledReason(`Claiming is paused`);
            return;
        }

        if(
            faucetData &&
            wallet.balances.fuse['0xc2E299b47398963c618De5b05C6BdeCd4cc64022'] +
            wallet.balances.fuse['0x7042807d547A2de0720421a6d2f629c9EB43E73D'] <
            (faucetData.minBalance / (10**18))) {

            setDisabled(true);
            setDisabledReason(`You need to hold at least ${(faucetData.minBalance / (10**18))} CNS/CNSONE to claim`);
            return;
        }

        const now = (new Date()).getTime();
        if(userFaucetData && userFaucetData.stats && userFaucetData.stats.timeCanClaim > parseInt(now/1000)) {
            setDisabled(true);
            setDisabledReason(`You can claim again at ${new Date(userFaucetData.stats.timeCanClaim * 1000).toLocaleString()}`);
            return true;
        }

        setDisabled(false);
        setDisabledReason(undefined);

    }, [wallet.address, faucetData, userFaucetData])

    useEffect(() => {
        getInfo();
        const interval = setInterval(getInfo, 5000)
        return () => clearInterval(interval);
    }, []);

    useEffect(getUserInfo, [wallet.address]);

    const claim = () => {
        if(wallet.address){
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(faucet_abi, FAUCET_ADDRESS);

            contract.methods
                .claim()
                .send({
                    from: wallet.address,
                    gasPrice: 10000000000
                })
                .once('transactionHash', function (tx) {
                    setPending(true)
                    console.log(tx)
                })
                .on('error', function (error) {
                    setPending(false)
                    console.log(error);
                })
                .then(function (receipt) {
                    getInfo();
                    getUserInfo();
                    setPending(false);
                });

        }
    }

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                {
                    faucetData !== undefined && <Grid item xs={12}>
                        <Grid item xs={12}>
                            <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                                <ListItem>
                                    <ListItemText
                                        primary={`${CoineusCryptoFormat(formatWeiToNumber(faucetData.claimable))} FUSE` || '--'}
                                        secondary={`Claimable Amount (${CoineusUSDFormat(formatWeiToNumber(faucetData.claimable) * wallet.prices.fuse.native?.inUSD)})`}
                                        primaryTypographyProps={{fontWeight: 700, fontSize: '1.75rem', textAlign: 'right'}}
                                        secondaryTypographyProps={{color: '#000', textAlign: 'right'}}
                                    />
                                </ListItem>
                            </Paper>
                            {/*TODO disable button based on params*/}
                            <Button
                                variant="contained"
                                fullWidth
                                style={{marginTop:20}}
                                disabled={disabled}
                                onClick={claim}
                            >CLAIM</Button>
                        </Grid>
                    </Grid>
                }

                {
                    disabled && <Grid item xs={12}>
                        <Alert severity="error" variant="filled">{disabledReason}</Alert>
                    </Grid>
                }

                {
                    faucetData !== undefined && <Grid item xs={12}>
                        <Divider style={{marginBottom: 10}}>OVERALL FAUCET STATS</Divider>
                        <Paper>
                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(faucetData.stats.claimedAllTime))} FUSE`}
                                    secondary="Claimed All-Time"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                                <ListItemText
                                    primary={`${CoineusUSDFormat(formatWeiToNumber(faucetData.stats.claimedAllTime) * wallet.prices.fuse.native?.inUSD)}`}
                                    secondary="Today's Value"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={faucetData.stats.numberOfClaimsAllTime}
                                    secondary="Number of Claims"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(faucetData.stats.averageClaim))} FUSE`}
                                    secondary="Average Claimed"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>

                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(faucetData.stats.highestClaimed))} FUSE`}
                                    secondary="Highest Amount Claimed All-Time"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>

                            <ListItem>
                                <ListItemText
                                    primary={faucetData.stats.highestClaimedBy}
                                    secondary="Highest Amount Claimed By"
                                    primaryTypographyProps={{fontWeight: 700, fontSize: '.75rem'}}
                                />
                            </ListItem>

                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(faucetData.minToClaim))} CNS/CNSONE`}
                                    secondary="Need To Hold To Claim"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>

                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(faucetData.delegatedAmount))} FUSE`}
                                    secondary="Delegated Amount"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>
                        </Paper>
                    </Grid>
                }

                {
                    userFaucetData !== undefined && <Grid item xs={12}>
                        <Divider style={{marginBottom: 10}}>MY FAUCET STATS</Divider>
                        <Paper>
                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(userFaucetData.stats.claimedAllTime))} FUSE`}
                                    secondary="Claimed All-Time"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                                <ListItemText
                                    primary={`${CoineusUSDFormat(formatWeiToNumber(userFaucetData.stats.claimedAllTime)* wallet.prices.fuse.native?.inUSD)}`}
                                    secondary="Today's Value"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />

                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={userFaucetData.stats.numberOfClaimsAllTime}
                                    secondary="Number of Claims"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(userFaucetData.stats.averageClaim))} FUSE`}
                                    secondary="Average Claimed"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>

                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(userFaucetData.stats.highestClaimed))} FUSE`}
                                    secondary="Highest Amount Claimed All-Time"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>

                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(formatWeiToNumber(userFaucetData.stats.lowestClaimed))} FUSE`}
                                    secondary="Lowest Amount Claimed All-Time"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                            </ListItem>
                        </Paper>
                    </Grid>
                }

            </Grid>

            <CoineusLoading open={pending} label="Transaction Pending"/>

        </Container>
    )
}